@import "./partials/params.scss";
@import "./partials/mixins.scss";
@import "./partials/animations.scss";
@import "./modal.scss";

:root {
  --font-family: "Jost";
}

body {
  min-width: 375px;
  font-family: "Jost";
  font-style: normal;
  font-weight: 500;
  @include clear;
  scroll-behavior: smooth;
  color: $main;
  overflow-x: hidden;
  width: 100%;
  background: #fff;
}
* {
  /*  font-family: "Montserrat";
  font-style: normal; */
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: none !important;
  line-height: 1.5;
}
/* main {
		overflow: hidden;
}
*/
a {
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  color: #08105b;
  text-decoration: none;
}
a:hover,
a:focus {
  text-decoration: none;
}
img {
  max-width: 100%;
  max-height: 100%;
  width: initial;
  height: initial;
}

@media (min-width: 1024px) {
  .onlyMobile {
    display: none !important;
  }
}
@include medium-screen {
  .onlyMobile {
    display: initial;
  }
  .onlyDesktop {
    display: none !important;
  }
}
.container {
  @include container;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.block {
  display: block;
}

.flex {
  display: flex;
}

.items-center {
  align-items: center;
}

.content-center {
  justify-content: center;
}

.content-between {
  justify-content: space-between;
}

.cursor-pointer {
  cursor: pointer;
}

.color-white,
.text-white {
  color: #fff;
}

.wrap {
  flex-wrap: wrap;
}

.w-half {
  width: 50%;
}

.w-20 {
  width: 20%;
}
.mt-100 {
  margin-top: 100px;
}

.mb-100 {
  margin-bottom: 100px;
}
.nowrap {
  white-space: nowrap;
}
.text-left {
  text-align: left !important;
}
.items-end {
  align-items: flex-end;
}

.text-center {
  text-align: center !important;
}
.w-full {
  width: 100%;
  max-width: 100%;
}
@media (max-width: 1023px) {
  .mobile-full {
    width: 100% !important;
  }
}

.formblock__input label {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: #373737;
  display: block;
  margin-bottom: 12px;
  opacity: 0.5;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="password"] {
  display: block;
  width: 100%;
  align-items: center;
  background-color: #f3f3f7;
  border-radius: 8px;
  display: flex;
  height: 50px;
  padding: 0 20px;
  width: 100%;
  border: none;

  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  border: 2px solid #f3f3f7;
  /* identical to box height */

  color: #282828;

  &.input-error {
    border-color: #f23f34;
  }

  &:focus {
    border-color: $focus;
  }
}

textarea {
  display: block;
  width: 100%;
  align-items: center;
  background-color: #f3f3f7;
  border-radius: 8px;
  display: flex;
  padding: 16px 20px;
  width: 100%;
  border: none;

  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  border: 2px solid #f3f3f7;
  /* identical to box height */

  color: #282828;

  &.input-error {
    border: 2px solid #f23f34;
  }

  &:focus {
    border: 2px solid #04a3e9;
  }
}

.input-errortext {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: #f23f34;
}

.inputpassword {
  position: relative;
}

.inputpassword img {
  position: absolute;
  right: 12px;
  top: 50%;
  cursor: pointer;
  transform: translateY(-50%);
}

.passwordforgot {
  margin-bottom: 33px;
}

.buttonsrow {
  flex-wrap: wrap;
}

.buttonsrow__item_full {
  margin-bottom: 8px;
}
.buttonsrow__item_full a {
  height: 60px;
  line-height: 60px;
}
.buttonsrow__item_full a.btn_border {
  height: 60px;
  line-height: 60px;
}

.passwordforgot a {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height */
  color: #04a3e9;
}

.passwordforgot .check label {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height */
  color: #282828;
}

.textlogin {
  margin-top: 35px;
}

.textlogin__text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height */
  color: #282828;
}

.textlogin__link a {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height */
}

.textinput {
  color: rgba(59, 67, 90, 0.5);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  margin-top: 15px;
}

.formblock__input {
  margin-bottom: 28px;
}

.formblock__check {
  margin: 21px 0;
}

.formblock__input_center {
  text-align: center;
}

.formblock__main_small {
  margin-bottom: 17px;
}

.modal-submit_small {
  margin-top: 41px;
}

.formblock__input:last-child {
  margin-bottom: 0;
}

.authpage__form {
  .formblock {
    min-width: 607px;
    max-width: 607px;
    box-shadow: 10px 10px 60px 0 rgba(55, 55, 55, 0.1);
    border-radius: 12px;

    padding: 60px 60px 50px 60px;
    background: #fff;
  }
}

.authpage__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: calc(100vh - 90px);
}

.authpage__image {
  margin-bottom: 32px;
}

.authpage__title {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 58px;
  line-height: 100%;
  color: #373737;
  margin-bottom: 35px;
}

.authpage__text {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 20px;
  line-height: 100%;
  color: rgba(55, 55, 55, 0.6);
}

.check label {
  cursor: pointer;
  display: inline-block;
  font-style: normal;
  font-weight: 500;
  margin-bottom: 0;
  font-size: 16px;
  line-height: 110%;
  color: #282828;
  padding-left: 28px;
  position: relative;
}

.formblock__input:last-child {
  margin-bottom: 0;
}

.check_rememb {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #2079ff;
  display: block;
}

.check input[type="checkbox"] {
  display: none;
}

.check label a {
  color: #282828;
  text-decoration: underline;
}

.check label a:hover {
  text-decoration: none;
}

.check label:before {
  background-color: $red;
  border-radius: 4px;
  content: "";
  height: 18px;
  left: 0;
  position: absolute;
  width: 18px;
  top: 50%;
  transform: translateY(-50%);
}

.check input[type="checkbox"]:checked + label:before {
  background-image: url(../img/checkbox_checked.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.btn_main {
  transition: $transition;
  display: inline-block;
  background: #f23f34;
  border-radius: 8px;
  line-height: 60px;
  padding: 0 20px;
  height: 60px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 18px;
  line-height: 100%;
  text-align: center;
  border: none;
  color: #fff;

  &:hover {
    background: #ff4d42;
  }

  &:disabled {
    opacity: 0.5;
  }
}

.buttonsrow {
  .buttonsrow__item_full {
    .btn_main {
      width: 100%;
    }
  }
}

.socials__text {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  color: #373737;
  margin-right: 26px;
}

.socials__item {
  margin-right: 22px;
  a {
    display: block;

    &:hover {
      transform: scale(1.15);
    }
  }
}

.formblock__socials {
  margin: 34px 0 14px 0;
}
.formblock__header {
  margin-bottom: 60px;
}
.formblock__link {
  margin-right: 40px;
  a {
    display: block;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 26px;
    line-height: 100%;
    color: #373737;
    opacity: 0.4;
    padding-bottom: 20px;
    border-bottom: 4px solid transparent;

    &.active,
    &:hover {
      opacity: 1;
      border-color: $red;
    }
  }
}

.footermain__text {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  color: #373737;
  opacity: 0.7;
  margin-left: 11px;
}

.footermain__content {
  .footermain__copy {
    margin-right: 108px;
  }
}

.footer__dev {
  a {
    display: block;
    span {
      display: block;
      margin-bottom: 2px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      color: #2e333e;
      opacity: 0.5;
    }
  }
}

.footermain__phone {
  margin-right: 19px;
  a {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 100%;
    color: #373737;
  }
}

.footermain__teltext {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  color: #373737;
}

.footermain__teltext {
  margin-left: 19px;
}

#footer {
  background: #f8f8f8;
  padding: 22px 0;
}

.formblock__icon {
  margin-bottom: 23px;
  text-align: center;
}

.formblock__text {
  text-align: center;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 26px;
  line-height: 100%;
  text-align: center;
  color: #373737;
}

.formblock__content {
  margin-bottom: 41px;
}
.formblock__link_icon {
  a {
    opacity: 1;
    border: none;
    display: flex;
    padding-bottom: 0;

    align-items: center;
    span {
      padding-left: 16px;
    }
  }
}

.headermain {
}
.headermain__wrapper {
}
.flex {
}
.items-center {
}
.content-between {
}
.headermain__menu {
}
.headermenu {
}
.headermenu__items {
}
.headermenu__item {
  margin-right: 10px;

  &:last-child {
    margin-right: 0;
  }
  a {
    display: flex;
    align-items: center;
    padding: 38px 23px;
    border-bottom: 5px solid transparent;

    span.headermenu__title {
      padding-left: 8px;
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 18px;
      line-height: 100%;
      color: #373737;
    }

    &:hover {
      border-color: $red;
    }
  }
}
.headermenu__item.active {
  a {
    color: #f23f34;
    border-color: $red;

    span {
      color: $red;
    }

    svg {
      path {
        fill: $red;
        opacity: 1;
      }
    }
  }
}
.headermenu__icon {
}
.headermenu__title {
}

.iconlink {
  position: relative;
  margin-right: 30px;
  &:last-child {
    margin-right: 0;
  }
}
.iconlink__link {
}
.iconlink__content {
  position: relative;
}
.iconlink__icon {
}

.iconlink__img {
  min-width: 50px;
  height: 50px;
  border: 2px solid #f8f8f8;
  border-radius: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  &:hover {
    border-color: $red;
  }
}
.iconlink__link:hover {
  .iconlink__img {
    border-color: $red;

    svg g {
      opacity: 1;
    }
  }
}
.iconlink__text {
  margin-left: 16px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  /* identical to box height */

  color: #212531;
}

.headermain__wrapper {
  position: relative;
}

.iconlink__count {
  display: block;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: $red;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  /* identical to box height */
  top: 0;
  right: 0;
  position: absolute;
  color: #ffffff;
  text-align: center;
}
$dropdownblock_top: 20px;
.dropdownblock {
  position: absolute;
  top: calc(100% + #{$dropdownblock_top});
  min-width: 412px;
  max-width: 412px;
  width: 100%;
  display: none;
}

.dropdownblock.active {
  display: block;
  z-index: 999;
}
.dropdownblock__wrapper {
  background: #ffffff;
  box-shadow: 10px 10px 60px 0 rgba(55, 55, 55, 0.16);

  border-radius: 8px;
  padding: 32px 16px 30px 32px;
}
.dropdownblock__links {
  ul {
    @include clear;
    li {
      margin-bottom: 29px;
      width: 100%;
      &:last-child {
        margin-bottom: 0;
      }
      a {
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 21px;
        /* identical to box height */
        color: #212531;
      }
    }
  }
}

.messageblock {
  min-width: 412px;
  right: 0;
  top: calc(100% + 10px);
}
.messageblock__items {
  max-height: 500px;
  overflow-x: hidden;
  overflow-y: auto;
  padding-right: 26px;
}
.messageblock__item {
  margin-bottom: 25px;
  border-bottom: 1px solid #f0f0f0;
  padding-bottom: 24px;
  &:last-child {
    margin-bottom: 0;
    border-bottom: none;
  }
}
.messageblock__status {
  min-width: 6px;
  min-height: 6px;
  margin-right: 8px;
  background: $red;
  border-radius: 50%;
}
.messageblock__item_unread {
  .messageblock__status {
    background: $red;
  }
}
.messageblock__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 13px;
}
.messageblock_titleleft {
  display: flex;
  align-items: center;
}
.messageblock_date {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: #373737;
  opacity: 0.4;
}
.messageblock__text {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
  color: #373737;

  opacity: 0.6;
  color: #212531;
}
.messageblock__info {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  color: #373737;
  opacity: 0.6;
}

.messageblock__status {
  display: none;
}

.messageblock__item_unread {
  .messageblock__status {
    display: block;
    margin-right: 8px;
  }
  .messageblock__info {
    opacity: 1;
  }
  .messageblock__text {
    opacity: 1;
  }
}

/* width */
::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: $red;
  /*  border-radius: 10px; */
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: $red;
}

.accountmenu {
  display: flex;
  align-items: center;
}
.accountmenu__name {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  /* @include dropdown; */
  margin-left: 15px;
}

.accountmenu__menu {
  right: 0;
  top: calc(100% + 20px);
  max-width: 230px;
  min-width: 230px;
}

