.newslist {
  height: 100%;
  padding: 34px 30px 30px 30px;
  position: relative;
  background: #fff;
  border: 1px solid #e9e9e9;
  border-radius: 8px;

  &:after {
    background: #fff;
    content: url(../img/angle.svg);
    position: absolute;
    z-index: 1;
    right: -1px;
    top: -1px;
  }
}

.newslist__title {
  margin-bottom: 36px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 22px;
  line-height: 100%;
  text-transform: uppercase;
  color: #373737;
}

.newslist__item {
  margin-bottom: 19px;
  /*   padding-bottom: 32px; */
  border-bottom: 1px solid #e9e9e9;
  min-height: 80px;
  margin-left: 15px;
  &:last-child {
    border-bottom: none;
    padding-bottom: 0;
    margin-bottom: 14px;
  }
}

.newslist__link {
  min-height: 44px;
  display: block;
  padding-left: 24px;
  border-left: 4px solid $red;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
  color: #373737;
}
.newslist__name {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
  color: #373737;
  overflow: hidden;

  text-overflow: ellipsis;
}
.newslist__date {
  display: block;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  color: #373737;
  margin-bottom: 7px;
  opacity: 0.7;
}
.btn_link {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  text-align: center;
  color: #f23f34;
  display: inline-block;
  border: 1px solid #f23f34;
  border-radius: 3px;
  padding: 13px 27px;

  &:hover {
    background: $red;
    color: #fff;
  }
}

.newslist__name {
  height: 34px;
  display: block;
}

.newslist_gray {
  background: #f8f8f8;
  padding: 24px 30px;
  border: none;

  &:after {
    display: none;
  }

  .newslist__name {
    height: 32px;

    line-height: 100%;
  }

  .newslist__date {
    margin-bottom: 0;
    margin-top: 13px;
  }

  .newslist__link {
    border-left: none;
    padding-left: 0;
  }
  .newslist__item {
    padding-top: 26px;
    margin-left: -30px;
    margin-right: -30px;
    width: calc(100% + 60px);
    border-bottom: 3px solid #fff;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 29px;
    margin-bottom: 0;

    &:last-child {
      margin-bottom: 26px;
    }
  }
}

