.custom-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.custom-table th,
.custom-table td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.custom-table th {
  background-color: #f2f2f2;
}

.custom-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.custom-table tr:nth-child(odd) {
  background-color: #fff;
}

.custom-table tr:not(:last-child) {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 10px; /*      */
}

.custom-table tr:last-child {
  border-bottom: none;
}

/*      */
.custom-table tr:first-child {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.custom-table tr td:first-child {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.custom-table tr td:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.custom-table button {
  padding: 8px 12px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-right: 5px;
}

/*    */
.custom-table td {
  padding-left: 20px;
  padding-right: 20px;
}

/*    */
.custom-table th {
  padding-left: 30px;
  padding-right: 30px;
}



.pagetable__table {
  thead {
    th {
      text-align: left;

      span {
        padding: 10px 21px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 100%;
        color: #373737;
        opacity: 0.5;
        display: block;
      }
    }
  }

  table {
    border-collapse: separate;
    border-spacing: 0 7px;
    width: 100%;
  }

  tbody {
    tr {
      background: #f5f5f5;
      border: none;
    }
    td {
      border: none;
      overflow: hidden;
      padding: 6px 42px 6px 21px;
    }
    td span {
      display: block;
    }
    td:last-child {
      span {
        padding-right: 6px;
      }
    }
    td:first-child {
      border-radius: 5px 0 0 5px;
    }
    td:last-child {
      border-radius: 0 5px 5px 0;
    }

    /*  .col_tableid {
      width: 200px;
    } */

    .col_tablestatus {
      width: 300px;
    }

    .col_tablename {
      max-width: 50%;
      width: 100%;
    }

    .col_tablebutton {
      width: 115px;
    }
  }
}


